@import url(https://fonts.googleapis.com/css?family=Raleway:400,700);
body {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
  color: inherit;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-color: #bcbab8;
  min-height: 100vh;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App footer {
  margin-top: 30px;
  font-family: 'Raleway', sans-serif;
}

.App footer a{
  display: inline-block;
  padding: 6px;
  font-weight: bold;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}

.App footer a:hover{
  color: #f9f9f9;
  -webkit-transform: rotate(5deg) scale(1.1);
          transform: rotate(5deg) scale(1.1);
  background-color: #625757;
  border-radius: 5px;
}

@media (max-width: 980px) {
  .App footer {
    position: absolute;
    bottom: 10px;
    right: 10px;
    font-size: 12px;
  }
}
.Panel {
    display: flex;
    width: 80%;
    height: 80vh;
    border-radius: 10px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.5);
    overflow: hidden;
    max-width: 1000px;
    max-height: 680px;
    min-height: 600px;
}

@media (max-width: 980px) {
    .Panel {
        width: 100%;
        height: 100vh;
        max-height: 100vh;
        box-shadow: none;
        border-radius: 0px;
    }
}

@media (max-width: 640px) {
    .Panel {
        flex-direction: column;
    }
}


.Sidebar {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    align-items: center;
    width: 20%;
    min-width: 180px;
    height: 100%;
    background-color: #625757;
    padding: 30px 0;
    box-sizing: border-box;
}

.Sidebar .Logo {
    display: block;
    width: 70%;
}

.Sidebar .Logo img{
    max-width: 100%;
}

.Sidebar .Controls {
    width: 70%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.Sidebar .Social {
    display: flex;
    justify-content: center;
    width: 70%;
    font-size: 2rem;
    color: #9d8f8f;
}

.Sidebar .Social a{
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
}

.Sidebar .Social a:hover {
    -webkit-transform: scale(1.2) rotate(5deg);
            transform: scale(1.2) rotate(5deg);
    color: #bcbab8;
}

.Sidebar .Social a:not(:last-child){
    margin-right: 20px;
}

.Sidebar .flex_container {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.Sidebar .flex_container .btn {
    display: flex;
    flex: 1 1;
    padding: 5px 10px;
    font-size: 24px;
}

.Sidebar .flex_container .btn:not(:last-child) {
    margin-right: 10px;
}


.Sidebar .Label {
    font-family: 'Raleway', sans-serif;
    color: #bcbab8
}


.Sidebar .btn{
    outline: none;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px 0;
    width: 100%;
    padding: 5px 10px;
    background-color: #9d8f8f;
    border-radius: 5px;
    border: none;
    font-size: 1rem;
    box-shadow: 0 2px 3px rgba(0,0,0,.35);
    cursor: pointer;
    box-sizing: border-box;
    -webkit-transition: all .2s ease;
    transition: all .2s ease;
}

.Sidebar .btn:hover {
    -webkit-transform: translateY(-2px);
            transform: translateY(-2px);
}

.Sidebar .btn.active {
    -webkit-transform: translateY(-2px) scale(1.1);
            transform: translateY(-2px) scale(1.1);
    background-color: #bcbab8;
}

@media (max-width: 980px) {
    .Sidebar .Logo {
        width: 60%;
    }

    .Sidebar .Label {
        font-size: 14px;
    }

    .Sidebar .Controls {
        width: 60%;
    }

    .Sidebar .Social {
        width: 60%;
        font-size: 1.8rem;
    }
}

@media (max-width: 640px) {
    .Sidebar {
        width: 100%;
        flex-direction: row;
        flex-wrap: wrap;   
        height: auto;
        padding: 15px 30px;
    }


    .Sidebar .Label {
        display: none;
    }

    .Sidebar .Logo {
        width: 30%;
    }

    .Sidebar .Controls {
        flex-direction: row;
        flex-wrap: wrap;
        order: 2;
        width: auto;
        flex: 1 0 100%;
        align-content: center;
        margin-top: 10px;
    }

    .Sidebar .btn{
        width: auto;
        flex: 1 1;  
    }

    .Sidebar .btn:not(:last-child) {
        margin-right: 20px;
    }

    .Sidebar .flex_container {
        flex: 1 1;
        margin-right: 20px;
    }

    .Sidebar .Social {
        width: 30%;
        font-size: 1.8rem;
        justify-content: flex-end;
    }
}

@media (max-width: 435px) {
    .Sidebar .btn:not(:last-child) {
        margin-right: 0px;
    }

    .Sidebar .flex_container {
        flex: 0 0 50%;
        margin-right: 20px;
    }
}
.Popover {
    position: absolute;
    top: 30%;
    left: 100%;
    z-index: 2;
}

@media (max-width: 640px) {
    .Popover {
        top: 100%;
        left: 30%;
    }
}
.PixelCount {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    margin: 10px 0;
}

.PixelCount span{
    flex: 1 1;
    outline: none;
    display: flex;
    justify-content: center;  
    padding: 5px 10px;
    background-color: #9d8f8f;
    border-radius: 5px;
    border: none;
    font-size: 1rem;
    box-shadow: 0 2px 3px rgba(0,0,0,.35);
    box-sizing: border-box;
}

.PixelCount .button {
    color: #111;
    margin-right: 5px;
    cursor: pointer;
    font-size: 1.5rem;
    -webkit-transition: all .2s ease;
    transition: all .2s ease;
}

.PixelCount .button:last-child {
    margin-right: 0px;
    margin-left: 5px;
}

.PixelCount .button:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

@media (max-width: 640px) {
    .PixelCount {
        width: auto;
        flex: 1 1;
        margin-right: 20px;
    } 
}

@media (max-width: 435px) {
    .PixelCount {
        flex: 1 1;
    } 
}
.Content {
    display: flex;
    flex-direction: column;
    flex: 1 1;
    background-color: #9d8f8f;
    justify-content: center;
    align-items: center;
}

.Content .btn {
    outline: none;
    font-size: 1em;
    margin: 30px 0 0 0;
    padding: 10px 20px;
    border-radius: 5px;
    background-color: #625757;
    border: none;
    color: #f9f9f9;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
    cursor: pointer;
    font-family: 'Raleway', sans-serif;
}

.Content .btn:hover {
   -webkit-transform: translateY(-1px);
           transform: translateY(-1px);
   box-shadow: 0 0 11px rgba(33,33,33,.4); 
}

.Content .btn:not(:last-child) {
    margin-right: 10px;
 }

.Content .btn i{
    margin-left: 5px;
}

@media (max-width: 780px) {
    .Content {
        padding: 30px 0;
    }
    .Content .btn {
        margin-top: 40px;
    }
}

@media (max-width: 640px) {
    .Content .btn {
        margin-top: 40px;
    }
}

.Canvas {
    box-sizing: content-box;
    border-radius: 2px;
    width: 500px;
    height: 500px;
    display: flex;
    justify-items: flex-start;
    flex-wrap: wrap;
    background-color: #f9f9f9;
}

.erase {
    cursor: context-menu;
}

@media (max-width: 780px) {
    .Canvas {
        width: 400px;
        height: 400px;
    }
}

@media (max-width: 435px) {
    .Canvas {
        width: 300px;
        height: 300px;
    }
}

.Pixel {
    display: block;
    height: auto;
    box-sizing: border-box;
    flex-basis: auto;
    margin: 0;
    padding: 0;
}
